<template>
 <div id="app">
    <div class="container">
        <div class="result">{{ results  ==''?0:results}}</div>
        <div class="calculated">{{ enter}}</div>
        <calculated :keyNumber='keyboard'></calculated>  
    </div>
 </div>
</template>
<script>
import calculated from './components/Calculated'
export default {
  data() {
    return{
      keyboard: ['+','-','/','*','=',9,8,7,6,5,4,3,2,1,0,'C']
      
    }
  },
  components: {
    calculated
  },
  computed:{
      results(){
        //通过this.$store获取仓库的数据result
        return this.$store.state.calculate.results;
      },
      enter(){
       //通过this.$store获取仓库的数据result
        return this.$store.state.calculate.enter;
      }
    }
  }
</script>
<style>
 *{padding: 0;
  margin: 0}
  #app{
    border: 1px #666 solid;
    width: 150px;
  }
</style>



