const state = {
  enter: '',
  results: ''
}
const mutations = {
  getResult(state, value) {
    if (value === '=') {
      state.results = eval(state.enter);
      state.enter += value;
    } else if (value === 'C') {
      state.enter = ''
    } else {
      state.enter += value
    }
  }
}
export default {
  state,
  mutations
}


