<template>
 <div id="Calculated">
  	<div v-for='item in keyNumber' class="key" @click='getValue'  :data-value="item">{{item}}</div>
 </div>
</template>
<script>
export default {
	props: {
		keyNumber: {
			type:Array,
			value:[]
	}
  },
  methods: {
  	getValue(event) {
  		let value = event.target.dataset.value
  		this.$store.commit('getResult',value)
  	}
  }
}
</script>
<style>
#Calculated{
	width: 150px;
}
 .key{
 	display: inline-block;
 	width: 50px;
 	height: 50px;
 	text-align: center;
 	line-height: 50px;

 }
</style>



